    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
}

.hover--card--enter img {
    position: relative;
    z-index: 99;
}

.hover--card--enter:hover .card-section {
    padding-left: 1rem !important;
    padding-right: 1rem !important;
    transition: 0.3s;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-duration: var(--animate-duration);
    animation-duration: var(--animate-duration);
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
}

.hover--card--enter:hover .card-section h1,
.hover--card--enter:hover .card-section h2,
.hover--card--enter:hover .card-section h3,
.hover--card--enter:hover .card-section h4,
.hover--card--enter:hover .card-section h5,
.hover--card--enter:hover .card-section h6,
.hover--card--enter:hover .card-section p,
.hover--card--enter:hover .card-section span,
.hover--card--enter:hover .card-section div {
    color: #ffad00 !important;
}

.hover--card--enter,
.hover--card--end {
    position: relative !important;
    overflow: hidden;
}

.hover--card--enter:hover::after,
.hover--card--end::after {
    content: '';
    width: 100%;
    height: 100%;
    opacity: 0.05;
    z-index: 5;
    background-color: #fff;
    position: absolute !important;
    top: 0rem !important;
    right: 0rem !important;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-duration: var(--animate-duration);
    animation-duration: var(--animate-duration);
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-duration: 0.3s;
    animation-duration: 0.3s;
}

.hover--card--enter:hover::after {
    -webkit-animation-name: slideInUp;
    animation-name: slideInUp;
}

.hover--card--end::after {
    -webkit-animation-name: slideOutDown;
    animation-name: slideOutDown;
}

.hover--card--filter img {
    filter: grayscale(80%) blur(1px);
}

.hover--card--filter:hover img {
    filter: grayscale(0) blur(0);
}

